const quizTitleRow = {
    props:["rowData", "index"],
    template : `
	<div style="padding:0 10px;">
		<div class="moku">
		<div>
			<div style="display:flex; justify-content: center;align-items: center; text-align:left">
				<img :src="this.$root.ICON_PATH + 'quiz_' + this.rowData.quiz_id + '.jpg?' + this.$root.reloadValue" style="width:50px; height:50px" class="icon">
				<span class="font_l" style="padding-left:10px;"><b>『{{ rowData.quiz_name }}』&nbsp;{{ quizSubName }}</b></span>
			</div>
			<div style="height:20px" />
			<div style="display:flex">
				<div class="bo2"  style="padding:15px 10px 10px; border-radius:10px; text-align:left; position:relative; width:90%; margin:auto; height:100px">
					<span class="font_m" style="position:absolute; top:-10px; left:20px; background-color:#ffffff; padding:0 10px">ひとこと</span>
					<span class="font_m">{{ this.rowData.description }}</span>
				</div>
				<div class="bo2" style="padding:10px 10px 0px; border-radius:10px; text-align:left; width:150px; margin-left:10px; height:100px">
					<img :src="this.$root.IMG_PATH + 'icon_quiz_count.png'" style="width:20px; height:20px">:
					<span class="font_m">{{ this.rowData.questions }}問</span>
					<div class="sp_line_ss" />
					<img :src="this.$root.IMG_PATH + 'icon_quiz_challenge.png'" style="width:20px; height:20px">:
					<span class="font_m">{{ this.rowData.challenge_count }}人</span>
					<div class="sp_line_ss" />
					<img v-show="this.rowData.twitter_id != ''" :src="this.$root.IMG_PATH + 'icon_twitter.png'" style="width:20px;margin-right:5px">
					<img v-show="this.rowData.url1 != ''" :src="this.$root.IMG_PATH + this.$root.snsIcon(this.rowData.url1) + '.png'" style="width:20px;margin-right:5px">
					<img v-show="this.rowData.url2 != ''" :src="this.$root.IMG_PATH + this.$root.snsIcon(this.rowData.url2) + '.png'" style="width:20px;margin-right:5px">
					<img v-show="this.rowData.url3 != ''" :src="this.$root.IMG_PATH + this.$root.snsIcon(this.rowData.url3) + '.png'" style="width:20px;margin-right:5px">
				</div>
			</div>
			<div class="sp_line_m" />
			<div style="display:flex; flex-wrap:wrap;" v-show="this.rowData.tag1 != ''">
				<span v-on:click="this.$root.pushTag(this.rowData.tag1)" v-show="this.rowData.tag1 != '' && this.rowData.tag1 != null" class="link">{{ this.$root.dispTag(this.rowData.tag1) }}</span>
				<span v-on:click="this.$root.pushTag(this.rowData.tag2)" v-show="this.rowData.tag2 != '' && this.rowData.tag2 != null" class="link" style="margin-left:20px">{{ this.$root.dispTag(this.rowData.tag2) }}</span>
				<span v-on:click="this.$root.pushTag(this.rowData.tag3)" v-show="this.rowData.tag3 != '' && this.rowData.tag3 != null" class="link" style="margin-left:20px">{{ this.$root.dispTag(this.rowData.tag3) }}</span>
				<span v-on:click="this.$root.pushTag(this.rowData.tag4)" v-show="this.rowData.tag4 != '' && this.rowData.tag4 != null" class="link" style="margin-left:20px">{{ this.$root.dispTag(this.rowData.tag4) }}</span>
				<span v-on:click="this.$root.pushTag(this.rowData.tag5)" v-show="this.rowData.tag5 != '' && this.rowData.tag5 != null" class="link" style="margin-left:20px">{{ this.$root.dispTag(this.rowData.tag5) }}</span>
			</div>
			<div class="sp_line_m" />
			<div v-show="this.rowData.isMyData" class="col-12 font_s" style="text-align:right">
				<div style="text-align:right">
					公開状況：
					<span v-show="this.rowData.publishing == 0">非公開</span>
					<span v-show="this.rowData.publishing == 1">公開</span>
					<span v-show="this.rowData.publishing == 2">限定公開</span>
					&nbsp;
					最終更新日：{{ createDate }} 
				</div>
				<div class="grayBar" />
				<div class="row" style="text-align:center">
					<div class="col-3">
						<img :src="this.$root.IMG_PATH + 'btnicon_edit.png'" style="width:30px; cursor:pointer" v-on:click="pushBtn('EDIT')">
					</div>
					<div class="col-3">
						<img :src="this.$root.IMG_PATH + 'btnicon_rank.png'" style="width:30px; cursor:pointer" v-on:click="pushBtn('SCORE')">
					</div>
					<div class="col-3">
						<img :src="this.$root.IMG_PATH + 'btnicon_copy.png'" style="width:30px; cursor:pointer" v-on:click="pushBtn('COPY')">
					</div>
					<div class="col-3">
						<img :src="this.$root.IMG_PATH + 'btnicon_twitter.png'" style="width:30px; cursor:pointer" v-on:click="pushBtn('SHARE')">
					</div>
				</div>
				<div class="sp_line_l" />
			</div>
			<div class="row" v-show="!this.rowData.isMyData" style="text-align:center">
				<div class="col-6">
					<button type="button" v-on:click="pushBtn('LINK')" class="btnBase" style="width:125px">LINKを見る</button>
				</div>
				<div class="col-6">
					<button type="button" v-on:click="pushBtn('PLAY')" class="btnBase" style="width:125px">挑戦する</button>
				</div>
				<div class="sp_line_m" />
				<div style="text-align:right">
					<span class="font_s">最終更新日：{{ createDate }}</span>
				</div>

			</div>
		</div>
		</div>
	</div>
	<br>`,
	data: function () {
			return {
			}
	},
	computed : {
		quizSubName : function() {
			let name = "のわたぷぴ";
			if (this.rowData.quiz_sub_name != "") {
				name = name + "（" + this.rowData.quiz_sub_name + "）";
			}
			return name;
		},
		createDate : function () {
			var date = new Date(this.rowData.created_at);
            var year = date.getFullYear();
            var month = date.getMonth() + 1;
            var day = date.getDate();
            return year + "/" + month + "/" + day;
		},
		iconUrl : function () {
            let iconUrl = this.rowData.icon;
            iconUrl = iconUrl.replace('{width}', '200');
            iconUrl = iconUrl.replace('{height}', '266');
			return iconUrl;
		},
	},
	methods : {
		pushBtn (val) {
			console.log("pushBtn" + val);

			if (val == "EDIT") {
				//編集
				console.log(this.rowData.quiz_id);
				this.$router.push("/edit/" + this.rowData.quiz_id);
				return;
			}
			this.$emit("pushRowBtn", val, this.index);
		},
	}
}

const popupWindow = {
    props:[],
    template : `
	<div id="overlay" v-show="isShowModal" v-on:click="closeOverlay($event)">
		<div id="content" style="text-align:center;" class="moku">
		<div>
			<p class="font_xl">{{ modalTitle }}</p>
			<div class="sp_line_xl" />
			<div v-show="this.body != ''">
				<div style="text-align:center; white-space: pre-line;" class="font_m">{{ this.body }}</div>
			</div>
			<div v-show="kbn == 'SCORE'" style="width:90%; margin:auto">
				<div class="row" style="text-align:center">
					<div class="col-7">
						<span class="font_m"><b>ニックネーム</b></span>
					</div>
					<div class="col-5">
						<span class="font_m"><b>スコア</b></span>
					</div>
				</div>
				<div class="grayBar" style="margin:5px 0" />
				<div v-for="(scoreData, index) in this.modalScoreList" :key="index">
					<div class="row" style="text-align:left">
					<div class="col-7" style="text-align:center">
							<span class="font_m">{{scoreData.name}}</span>
						</div>
						<div class="col-5" style="text-align:right">
							<span class="font_m">{{scoreTen(scoreData.score, scoreData.q_count)}}点({{scoreData.score}} / {{ scoreData.q_count }})</span>
						</div>
					</div>
					<div style="text-align:right">
						<span class="font_s">{{ scoreTime(scoreData.time) }}</span>
					</div>
					<div v-show="index != this.modalScoreList.length - 1" class="grayBar" style="margin:5px 0" />
				</div>
			</div>
			
			<div v-show="kbn == 'LINK'">
				<div v-for="(url, index) in this.modalLinkList" :key="index" style="width:100%;" v-on:click="pushModalLink(url)">
					<div class="row" style="text-align:left; height:40px; padding:5px; cursor:pointer">
						<div class="col-10" style="display:flex; vertical-align:middle">
							<img :src="this.$root.IMG_PATH + this.$root.snsIcon(url) + '.png'" style="width:30px; height:30px;vertical-align:middle">
							<span class="font_s" style="vertical-align:middle">{{url}}</span>
						</div>
						<div class="col-2">
							<img :src="this.$root.IMG_PATH + 'btn_link_arrow.png'" style="height:30px">
						</div>
					</div>
					<div class="grayBar" v-show="this.modalLinkList.length != 1 && index != this.modalLinkList.length - 1" />
				</div>
			</div>

			<div v-show="this.kbn == 'CATEGORY'">
				<div class="row" style="text-align:left; height:40px; padding:5px; cursor:pointer"  v-on:click="pushCategory(0)">
					<div class="col-10" style="display:flex">
						<img :src="this.$root.IMG_PATH + 'icon_category_personal.png'" style="width:30px; height:30px;">
						<span class="font_m" style="margin:5px 0 0 10px">パーソナル</span>
					</div>
					<div class="col-2">
						<img :src="this.$root.IMG_PATH + 'btn_link_arrow.png'" style="height:30px">
					</div>
				</div>
				<div class="grayBar" />
				<div class="row" style="text-align:left; height:40px; padding:5px; cursor:pointer"  v-on:click="pushCategory(1)">
					<div class="col-10" style="display:flex">
						<img :src="this.$root.IMG_PATH + 'icon_category_like.png'" style="width:30px; height:30px;">
						<span class="font_m" style="margin:5px 0 0 10px">好き / 嫌い</span>
					</div>
					<div class="col-2">
						<img :src="this.$root.IMG_PATH + 'btn_link_arrow.png'" style="height:30px">
					</div>
				</div>
				<div class="grayBar" />
				<div class="row" style="text-align:left; height:40px; padding:5px; cursor:pointer"  v-on:click="pushCategory(2)">
					<div class="col-10" style="display:flex">
						<img :src="this.$root.IMG_PATH + 'icon_category_entertainment.png'" style="width:30px; height:30px;">
						<span class="font_m" style="margin:5px 0 0 10px">エンタメ</span>
					</div>
					<div class="col-2">
						<img :src="this.$root.IMG_PATH + 'btn_link_arrow.png'" style="height:30px">
					</div>
				</div>
				<div class="grayBar" />
				<div class="row" style="text-align:left; height:40px; padding:5px; cursor:pointer"  v-on:click="pushCategory(3)">
					<div class="col-10" style="display:flex">
						<img :src="this.$root.IMG_PATH + 'icon_category_hoby.png'" style="width:30px; height:30px;">
						<span class="font_m" style="margin:5px 0 0 10px">趣味 / 特技</span>
					</div>
					<div class="col-2">
						<img :src="this.$root.IMG_PATH + 'btn_link_arrow.png'" style="height:30px">
					</div>
				</div>
				<div class="grayBar" />
				<div class="row" style="text-align:left; height:40px; padding:5px; cursor:pointer"  v-on:click="pushCategory(4)">
					<div class="col-10" style="display:flex">
						<img :src="this.$root.IMG_PATH + 'icon_category_episode.png'" style="width:30px; height:30px;">
						<span class="font_m" style="margin:5px 0 0 10px">エピソード</span>
					</div>
					<div class="col-2">
						<img :src="this.$root.IMG_PATH + 'btn_link_arrow.png'" style="height:30px">
					</div>
				</div>
				<div class="grayBar" />
				<div class="row" style="text-align:left; height:40px; padding:5px; cursor:pointer"  v-on:click="pushCategory(5)">
					<div class="col-10" style="display:flex">
						<img :src="this.$root.IMG_PATH + 'icon_category_lifestyle.png'" style="width:30px; height:30px;">
						<span class="font_m" style="margin:5px 0 0 10px">ライフスタイル</span>
					</div>
					<div class="col-2">
						<img :src="this.$root.IMG_PATH + 'btn_link_arrow.png'" style="height:30px">
					</div>
				</div>
			</div>
			
			<div v-show="this.kbn == 'TEMPLATE'">
				<div v-for="(tempData, index) in this.modalTemplateList" :key="index" v-on:click="pushTemplate(tempData.temp_body)" style="padding:0px 10px">
					<div class="container-fluid">
						<div class="row">
							<div class="col-10" style="text-align:left">
								<span class="font_m">{{tempData.temp_body}}</span>
								<img v-show="tempData.special != 0" :src="temp_mark_src(tempData.special)" style="width:25px; height: 17px; margin-left:10px">
							</div>
							<div class="col-2" style="text-align:right">
								<img :src="this.$root.IMG_PATH + 'btn_link_arrow.png'" style="height:30px">
							</div>
						</div>
					</div>
					<div class="grayBar" v-show="index != this.modalTemplateList.length - 1" />
				</div>
			</div>

			<div v-show="this.kbn != 'NICKNAME'">
				<div class="sp_line_xl" />
				<div v-show="this.kbn == 'TEMPLATE'">
					<button type="button" v-on:click="backCategory" class="btnBase" style="width:90%">カテゴリーに戻る</button>
					<div class="sp_line_m" />
				</div>
				<button v-on:click="closeModal()" class="btnBase" style="width:90%">{{ closeBtnName }}</button>
			</div>
		</div>
		</div>
	</div>
	`,
	data: function () {
		return {
			kbn : "",
			body : "",
			nickName : "",
			modalLinkList : [],
			modalAllTemplateList : [],
			modalTemplateList : [],

			modalScoreList: [],

			isShowModal : false,
			selCategoryIndex : -1,
		}
	},
	computed : {
		temp_mark_src () {
			return (special) => {
				let markPath = this.$root.IMG_PATH + "mark/";
				if (special == 1) {
					//おすすめ
					console.log(markPath + "temp_osusume.png");
					return markPath + "temp_osusume.png";
				} else if (special == 2) {
					//注意
					console.log(markPath + "temp_tyuui.png");
					return markPath + "temp_tyuui.png";
				}
				return "";
			}
		},
		scoreTen () {
			return (correct, length) => {
				return Math.floor(correct / length * 100);
			}
		},
		scoreTime () {
			return (dateStr) => {
				console.log(dateStr);
				var date = new Date(dateStr);
				// var year = date.getFullYear();
				var month = date.getMonth() + 1;
				var day = date.getDate();
				var hour = date.getHours();
				var min = date.getMinutes();
	
				return ('0' + month).slice(-2) + "/" + ('0' + day).slice(-2) + " " + ('0' + hour).slice(-2) + ":" + ('0' + min).slice(-2);
			}
		},
		modalTitle () {
			if (this.kbn == "LINK") {
				return "リンクを選択";
			} else if (this.kbn == "COPY") {
				return "";
			} else if (this.kbn == "CATEGORY") {
				return "カテゴリー選択";
			} else if (this.kbn == "TEMPLATE") {
				if (this.selCategoryIndex == 0) {
					return "パーソナル";
				} else if (this.selCategoryIndex == 1) {
					return "好き/嫌い";
				} else if (this.selCategoryIndex == 2) {
					return "エンタメ";
				} else if (this.selCategoryIndex == 3) {
					return "趣味/特技";
				} else if (this.selCategoryIndex == 4) {
					return "エピソード";
				} else if (this.selCategoryIndex == 5) {
					return "ライフスタイル";
				}
			} else if (this.kbn == "SCORE") {
				return "みんなのスコア";
			} else if (this.kbn == "NICKNAME") {
				return "ニックネーム"
			}
		},
		closeBtnName () {
			if (this.kbn == "UPDATE") {
				return "TOPに戻る";
			}
			return "閉じる";
		},
	},
	methods : {
		test : function() {
			console.log("test");
		},
		showModal : function(kbnStr) {
			document.getElementById('body').className = "modalBg";
			this.kbn = kbnStr;
			this.isShowModal = true;
		},
		pushModalLink : function (link) {
			window.open(link, '_blank');
		},
		stopModalEvent: function(event) {
			event.stopPropagation()
		},
		pushCategory : function (catIndex) {
			console.log(catIndex);
			//選択したカテゴリでテンプレートを構築
			this.modalTemplateList = [];
			for (let i = 0; i < this.modalAllTemplateList.length; i++) {
				console.log(this.modalAllTemplateList[i].category + " " + catIndex);
				if (this.modalAllTemplateList[i].category == catIndex) {
					this.modalTemplateList.push(this.modalAllTemplateList[i]);
				}
			}
			this.selCategoryIndex = catIndex;

			//テンプレート一覧へ切替
			this.kbn = "TEMPLATE";
		},
		pushTemplate : function (tempBody) {
			// this.questionList[this.tempSelIndex]["quiz_body"] = tempBody;
			console.log(this.$router);
			this.closeModal(false);
			this.$emit('pushTemp', tempBody);
		},
		pushPlay : function (val) {
			this.closeModal(false);
			this.$emit('pushPlay', val, this.nickName);
		},
		closeModal: function() {
			document.getElementById('body').className = "";
			//モーダルを閉じる
			console.log(this.kbn);
			this.isShowModal = false;
			console.log(this.kbn);

			if (this.kbn == "UPDATE") {
				//TOPへ
				this.$router.push("/");
			}
		},
		closeOverlay : function (e) {
			if (e.target.id == "overlay") {
				if (this.kbn == "UPDATE" || this.kbn == "NICKNAME") {
					//更新時は無効
					return;
				}
				this.isShowModal = false;
			}
		},
		backCategory : function() {
			this.kbn = "CATEGORY";
		}

	}
}


createApp(App)
.component("quiz-title-row", quizTitleRow)
.component("popup-window", popupWindow)
.use(router, axios)
.mount('#app')
