<template>
  <div class="header" style="background-color:#9D7987; text-align:right;">
    <div class="container-fluid">
      <div class="row" style="height:50px">
        <div class="col-6" style="text-align:left">
          <img :src="IMG_PATH + 'icon_logo.png'" v-on:click="pushHome" style="width:150px;">
        </div>
        <div class="col-6" style="justify-content:right; display:flex; align-items:center;">
          <div v-show="this.isLogin">
            <img :src="this.$root.ICON_PATH + 'user_' + this.userId + '.jpg'" style="height:45px; border-radius: 50%;"> 
            <button type="button" v-on:click.prevent.stop="pushLogout()" style="margin:5px" class="btnBase">ログアウト</button>
          </div>
        </div>
      </div>
    </div>
  </div>
  <div class="mainArea" style="margin:auto;">
    <router-view/>
  </div>
  <div class="footer"  style="background-color:#9D7987;padding: 30px 0">
  </div>
</template>

<script>
  import { getAuth, signOut } from 'firebase/auth'
  import axios from 'axios'

  export default {
    data() {
      return {
        API_SUCCESS : 200,
        API_FAILED_LOGIN : 99,
        API_FAILED_AUTH : 100,
        API_FAILED_PARAM : 300,
        API_FAILED_NODATA : 400,
        API_FAILED_DUPLICATE : 401,
        API_FAILED_MISMATCH : 402,
        API_FAILED_PRIVATE : 500,
        API_FAILED_FILE : 600,
        API_FAILED_EXEPTION : 900,

        isLogin: false,
        userId : "",
        displayName : "",
        twitterId : "",

        IMG_PATH : "./app/img/",
        ICON_PATH : "./storage/icon/",
        CARD_PATH : "./storage/card/",

        // SERVER_PATH : "http://localhost:8001/api/",
        // ROOT_PATH : "./",
        // DOMAIN_PATH : "localhost",

        SERVER_PATH : "https://iphone-taro.sakura.ne.jp/Watapupi/api/",
        ROOT_PATH : "https://iphone-taro.sakura.ne.jp/Watapupi/",
        DOMAIN_PATH : "iphone-taro.sakura.ne.jp",

        pageName : "",
        reloadValue : "", //画像の更新用,

        mainBg : "backgroundBase",

        mainBgs : {
          "normal" : "backgroundBase",
          "correct" : "backgroundCorrect",
          "incorrect" : "backgroundIncorrect",
        }

      }
    },
    mounted () {
    },
    computed : {
      dispTag () {
        return (tagName) => {
          return "#" + tagName;
        }
      },
      snsIcon () {
        return (url)=>{
          if (url == null) {
            return "";
          }

          url = url.toLowerCase();
          if (url.indexOf("youtube") > -1) {
            return "icon_youtube";
          } else if (url.indexOf("twitter") > -1) {
            return "icon_twitter";
          } else if (url.indexOf("twitch") > -1) {
            return "icon_twitch";
          }
          return "icon_page";
        }
      },
    },
    methods : {
      setBg : function (val) {
        this.mainBg = this.mainBgs[val];
      },
      checkServerResponse : function (response) {
        //サーバー返答時の共通処理
        console.log(response.data);
        let resData = response.data;
        if (resData["status"] == this.API_FAILED_AUTH) {
          console.log("未認証des"); 
          //未認証エラー
          this.isLogin = false;
          this.$router.push("/");
          return false;
        } else if (resData["status"] != this.API_SUCCESS) {
          //エラー
          this.$router.push({path : "/error", query : {errorCode : resData["status"], message : resData["errMsg"]}});
          return false;
        }

        if (resData["isLogin"] == "") {
          //未ログイン
          this.isLogin = false;
        } else {
          this.isLogin = true;
          this.userId = resData["isLogin"];
          this.twitterId = localStorage.twitterId;
          this.displayName = localStorage.displayName
        }
        console.log("BBBBBBBBBBBBB");
        return true;
      },
      pushLogout : function () {
          axios.get(this.$root.SERVER_PATH + 'logout')
          .then((response) => {
            let resData = response.data;
            console.log(resData["status"] );
          
            if (!this.$root.checkServerResponse(response)) {
              return;
            }
            
            signOut(getAuth());
            // this.userData = {
            //   userId : "",
            //   userName : "",
            //   profile : "",
            // };
            this.$router.push('/');
            this.isLogin = false;
          })
          .catch((e) => {
            alert(e);
          });

      },
      pushTag : function (val) {
			this.$router.push({name: "Top", query : { keyword: val, order: '0'}});
      },
      pushHome : function () {
        if (this.$root.pageName == "Top") {
          location.reload();
        } else {
          this.$router.push("/");
        }
      },
      reloadImgValue : function() {
        let val = new Date();
        this.reloadValue = val;
      }
    }
  }
</script>



