<template>
<div class="top" style="padding:10px">
  <div style="text-align:center">
    <span class="font_m">あなたに関するクイズを作成して、みんなにあなたのことをもっと知ってもらいましょう。</span>
  </div>
  <div class="sp_line_l"/>
  <div v-show="this.$root.isLogin">
    <p class="font_xl">あなたのクイズ</p>
    <quiz-title-row v-for="(myQuizData, index) in myQuizDataList" :key="index" v-bind:rowData='myQuizData' v-bind:index=index @pushRowBtn="pushRowBtn"></quiz-title-row>
    
    <button type="button" class="btnBase font_l" style="width:80%; padding:10px 10px;" v-on:click="pushCreateQuiz()">新しくクイズを作る</button>
  </div>
  <div v-show="!this.$root.isLogin">
    <img :src="this.$root.IMG_PATH + 'top_senden.png'" style="width:80%">
    <div class="sp_line_l"/>
    <div style="width:60%; background-color:#ffffff; padding:15px 10px; border: solid 3px; border-radius:10px; margin:auto; box-shadow: 3px 3px 0px 0 rgba(0, 0, 0, .2);">
      <p class="errMsg" v-if="errorMessageData.login">▼ {{errorMessageData.login}}<br></p>
      <button class="btnBase" type="button" v-on:click="pushLogin(1)" style="width: 80%">ログイン</button>
      <div class="grayBar" />
      <p class="font_m">アカウントを作成していない方はこちら</p>
      <div class="sp_line_s"/>
      <div>
        <input type="checkbox" id="term" name="term" v-model="isTerm" style="vertical-align:middle">
        <span  class="font_m" style="margin-left:10px"><a href="#">利用規約</a>に同意します</span>
      </div>
      <div class="sp_line_s"/>
      <button v-show="isTerm" class="btnBase" type="button" v-on:click="pushLogin(0)" style="width:80%">新規登録</button>
      <p v-show="!isTerm" class="btnBase btnNegative" style="width:80%; margin:auto">新規登録</p>
    </div>
  </div>
  <div class="sp_line_xl" />
  
  <p class="font_xl">あなたのクイズ</p>
  <div class="sp_line_m" />

  <div class="input-group" style="width:70%; margin:auto">
    <input type="text" style="border-right-width: 0px; border-radius:6px 0 0 6px" v-model="keyword" class="form-control bo2" placeholder="名前、タグなど" aria-label="名前、タグなど">
    <button class="btnBase" type="button" id="button-addon2" v-on:click="pushSearch">検索</button>
  </div>

  <div class="sp_line_xl" />

  <div class="bo2" style="padding:15px 10px 10px; border-radius:10px; text-align:left; position:relative; width:90%; margin:auto" v-show="this.hotTagList.length != 0">
    <span class="font_m" style="position:absolute; top:-10px; left:20px; background-color:#FFDDCE; padding:0 10px">人気のタグ</span>
    <div style="display:flex; flex-wrap:wrap">
        <div style="display:flex" v-for="(tagInfo, index) in this.hotTagList" :key="index">
          <span class="link" v-on:click.prevent.stop="pushTag(tagInfo.tag)">{{this.$root.dispTag(tagInfo.tag)}}</span>&nbsp;&nbsp;
        </div>
    </div>
  </div>

  <br>
  <div style="text-align:right; margin-right:20px">
    並び順&nbsp;:&nbsp;
    <span v-show="this.order != '0'" class="link" v-on:click="pushOrder('0')" style="margin:5px">新着順</span>
    <span v-show="this.order == '0'" style="color:gray; margin:5px">新着順</span>
    &nbsp;/&nbsp;
    <span v-show="this.order != '1'" class="link" v-on:click="pushOrder('1')" style="margin:5px">人気順</span>
    <span v-show="this.order == '1'" style="color:gray; margin:5px">人気順</span>
  </div>
  <div v-for="(quizData, index) in this.dispDataList" :key="index">
    <quiz-title-row  v-bind:rowData='quizData' v-bind:index=index @pushRowIcon="pushRowIcon" @pushRowBtn="pushRowBtn"></quiz-title-row>
  </div>
  <span  v-show="this.isNext == true" v-on:click.prevent.stop="pushNext" class="showMore" style="width:300px;display:inline-block;margin-top:10px;padding:10px">Show more</span>
</div>
<popup-window ref="popup"></popup-window>

</template>

<script>
import { getAuth, setPersistence, TwitterAuthProvider, signInWithPopup, browserLocalPersistence } from 'firebase/auth'
import axios from 'axios'

export default {
  name: 'Top',
  components: {
  },
  data() {
    return {
      pageIndex : 1,
      isNext : false,
      
      dispDataList: [],
      allQuizDataList: [],

      hotTagList: [],

      isTerm : false,
      keyword : "",
      order : "0",
      
      myQuizDataList : [],
      show : false,

      myQuizScoreList : {},

      modalLinkList : [],

      errorMessageData : {
        login:"",
      }
    };
  },
  computed : {
		
  },
  mounted () {
    window.addEventListener('scroll', () => {
        this.show = (window.scrollY > 150);
    });
  },
  methods : {
    pushMyBtn : function (kbn) {
      if (kbn == "COPY") {
        this.$refs.popup.showModal("COPY");
      }
    },
    pushCreateQuiz : function() {
      console.log("in");
      let params = new URLSearchParams();
      params.append("displayName", this.$root.displayName);
      params.append("twitterId", this.$root.twitterId);
      console.log(this.$root.displayName);
      axios.post(this.$root.SERVER_PATH + "createQuiz", params)
      .then((response) => {
        let resData = response.data;
        console.log(resData);
        if (!this.$root.checkServerResponse(response)) {
          return;
        }

        let quizId = resData["quizId"];
        this.$router.push("/edit/" + quizId);
      })
      .catch((e) => {
        alert(e); 
      });
    },
    pushRowBtn : function (val, index) {
      console.log(val + " " + index);
			if (val == "SCORE") {
        console.log("IN");
				//自分 みんなの結果
        let quizId = this.myQuizDataList[index].quizId;
        console.log(this.myQuizScoreList[quizId]);
        if (this.myQuizScoreList[quizId] == null) {
          //未取得
          this.getScoreData(quizId);
          return;
        } else {
          //取得済み
          let scoreData = this.myQuizScoreList[quizId];

          //経過時間を調べる
          let currentTime = new Date(scoreData["currentTime"]).getTime();
          if ((currentTime - (new Date()).getTime()) / 1000 >  300) {
            //5分経過 再取得
            console.log("時間経過で再取得");
            this.getScoreData(quizId);
            return;
          }
          console.log("使いまわし");

          //そのまま表示
          this.$refs.popup.modalScoreList = scoreData["dataList"];
          this.$refs.popup.showModal("SCORE");

        }
      } else if (val == "COPY") {
        //自分 コピー
        this.$refs.popup.body = "URLをコピーしました";
        this.$refs.popup.showModal("COPY");
      } else if (val == "SHARE") {
        //自分 シェア

      } else if (val == "PLAY") {
				//みんな クイズに挑戦
        let quizData = this.dispDataList[index];
        let quizId = quizData["quiz_id"];
        this.$router.push('/play/' + quizId);
			} else if (val == "LINK") {
        //みんな リンク
        this.modalLinkList = [];
        let rowData = this.dispDataList[index];
        this.modalLinkList.push("https://twitter.com/" + rowData["twitterId"]);

        if (rowData["url1"] != "") {
          this.modalLinkList.push(rowData["url1"]);
        }
        if (rowData["url2"] != "") {
          this.modalLinkList.push(rowData["url2"]);
        }
        if (rowData["url3"] != "") {
          this.modalLinkList.push(rowData["url3"]);
        }
        this.$refs.popup.modalLinkList = this.modalLinkList;
        this.$refs.popup.showModal("LINK");
      }
    },
    getScoreData : function (quizId) {
      console.log("取得");
      let params = new URLSearchParams();
      params.append('quizId', quizId);
      
      axios.post(this.$root.SERVER_PATH + "getScoreData", params)
      .then((response) => {
        let resData = response.data;
        if (!this.$root.checkServerResponse(response)) {
          return;
        }
        console.log("aaaa");
        //リストに登録
        this.myQuizScoreList[quizId] = resData["scoreData"];
        console.log(resData["scoreData"]);

        this.$refs.popup.modalScoreList = resData["scoreData"]["dataList"];
        this.$refs.popup.showModal("SCORE");

      }).catch((e) => {
        alert(e); 
      });
    },
    pushSearch : function () {
      console.log("search " + this.keyword);
      let query = Object.assign({}, this.$route.query);
      let keyword = this.keyword;

      if (query["keyword"] == keyword && query["order"] == this.order) {
        //同じキーワード
        console.log("Sおなじ");
        this.initAction("0");
      } else {
        console.log("Sちがう");
        query["keyword"] = keyword;
        this.$router.push({query});
      }
    },
    pushTag : function (tag) {
      console.log("tag " + tag);
      let query = Object.assign({}, this.$route.query);
      if (query["keyword"] == tag && query["order"] == this.order) {
        //同じタグを押された場合
        console.log("Tおなじ");
        this.initAction("0");
      } else {
        console.log("Tちがう");
        query["keyword"] = tag;
        this.$router.push({query});
      }
    },
    pushOrder : function (order) {
      let query = Object.assign({}, this.$route.query);
      console.log("order " + order + " " + query["order"]);
      if (query["order"] == order) {
        //同じタグを押された場合
        console.log("Oおなじ");
      } else {
        console.log("Oちがう");
        query["order"] = order;
        this.$router.push({query});
      }
    },
    initAction : function () {
      this.allQuizDataList = [];
      this.myQuizScoreList = [];
      
      let query = Object.assign({}, this.$route.query);
      // let curPage = query["page"];
      let keyword = query["keyword"];
      let order = query["order"];
      console.log(keyword + " ---- " + order);

      if (keyword == null) {
        keyword = "";
      }
      if (order == null) {
        order = "0";
      }
      
      this.keyword = keyword;
      this.order = order;

      let params = new URLSearchParams();
      params.append('order', order);
      params.append('keyword', keyword);
      
      console.log("IN");
      axios.post(this.$root.SERVER_PATH + "getQuizList", params)
      .then((response) => {
        let resData = response.data;
        if (!this.$root.checkServerResponse(response)) {
          return;
        }

        let myDataList = resData["myQuizList"];
        if (myDataList != null) {
          this.myQuizDataList = myDataList;

          this.$root.isLogin = true;
        }

        this.pageIndex = 1;
        this.allQuizDataList = resData.quizList;
        this.hotTagList = resData.hotTags;

        //1ページ分を
        this.dispList();
      })
      .catch((e) => {
        alert(e); 
      });
    },
    dispList : function () {
      let max = 10 * this.pageIndex;
      if (max > this.allQuizDataList.length) {
        max = this.allQuizDataList.length;
        this.isNext = false;
      } else {
        this.isNext = true;
      }

      this.dispDataList = [];
      for (let i = 0; i < max; i++) {
        this.dispDataList.push(this.allQuizDataList[i]);          
      }
    },
    pushNext : function () {
      this.pageIndex = this.pageIndex + 1;
      this.dispList();
    },
    pushLogin : function (val) {
      const provider = new TwitterAuthProvider()
      const auth = getAuth();
      setPersistence(auth, browserLocalPersistence)
      .then(() => {
        return signInWithPopup(auth, provider)
        .then((result) => {
          console.log(result);

          let displayName = result._tokenResponse.displayName;
          let displayId = result._tokenResponse.screenName;
          console.log(displayName + " " + displayId + " " + result._tokenResponse.localId + " " + result._tokenResponse.photoUrl);

          let params = new URLSearchParams();
          // params.append('twitterUID', 'IbLuEgfCgTfhSoOHMFi8h6hLrry1');
          params.append('twitterUID', result.user.providerData[0].uid);
          params.append('twitterIconUrl', result._tokenResponse.photoUrl);
          params.append('displayName', displayName);
          params.append('twitterId', displayId);
          if (val == 0) {
            //新規登録
            params.append('kbn', 'NEW');
          } else {
            //ログイン
            params.append('kbn', 'LOGIN');
          }

          axios.post(this.$root.SERVER_PATH + "login", params)
          .then((response) => {
            let resData = response.data;
            if (resData["status"] == this.$root.API_FAILED_LOGIN) {
              //IDとPASSでログイン失敗
              this.errorMessageData.login = "ログインに失敗しました";
              return;
            } else if (resData["status"] == this.$root.API_FAILED_NODATA) {
              //データなし（アカウント未作成）
              this.errorMessageData.login = "アカウントが作成されていません\n新規登録を行ってください";
              return;
            }

            if (!this.$root.checkServerResponse(response)) {
              return;
            }

            localStorage.setItem("twitterId", displayId);
            localStorage.setItem("displayName", displayName);
            
            this.$root.displayName = displayName;
            this.$root.twitterId = displayId;

            let myDataList = resData["myQuizList"];
            if (myDataList != null) {
              this.myQuizDataList = myDataList;
              console.log(this.myQuizDataList);
              this.$root.isLogin = true;
            }
          })
          .catch((e) => {
            alert(e); 
          });
        }).catch((error) => {
          console.log(error.response);
        });
      }).catch((error) => {
        console.log(error.response);
      });
    },
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      console.log("A");
      vm.initAction();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      console.log("B");
      vm.initAction();
    });
  },
  watch : {
    '$route'(to, from) {
      if (to["name"] == from["name"] && to["name"] == "Top") {
        if (to.query["keyword"] != from.query["keyword"] || to.query["order"] != from.query["order"]) {
          //値が違う場合のみ
          console.log("C");
          this.initAction();
        } else if (to.query["keyword"] == null || to.query["order"] == null) {
          console.log("1EEEEEEEEEEEEEEEEEEEEEEEEEEEEEEE");
        }
      } else {
          console.log("2EEEEEEEEEEEEEEEEEEEEEEEEEEEEEEEE"); 
      }
    }
  }


}
</script>

<style>
.loginFrameBase {
	width: 80%;
	margin: 0 auto;
	text-align: center;
	color: #5c87a6;
}

a.loginFrame {
  display: block;
	padding: 20px;
	text-decoration: none;
	color: #333;
	border-radius: 10px;
	border: solid 3px #ccc;
	background-color: #fff;
}

a.loginFrame:hover {
  background-color: #fffdc5;
	text-decoration: none;
}

</style>