<script>
import axios from 'axios'

export default {
  name: 'Result',
  components: {
  },
  data() {
    return {
      langIndex : 0,
      btnOn : "btn-primary",
      btnOff : "btn-secondary",
      pageIndex : 1,
      searchWord : "",
      isNext : false,
      searchLabel : "Top Games",

      dispDataList: [],
      filterDataList : [],
      allRowDataList: [],

      show : false,
    };
  },
  mounted () {
    window.addEventListener('scroll', () => {
        this.show = (window.scrollY > 150);

    });
  },
  methods : {
    updateList : function (e) {
      this.searchWord = e.target.value;
      if (this.searchWord == "") {
        //検索ワードなし
        this.filterDataList = this.allRowDataList;
        this.searchLabel = "Top Games (" + this.filterDataList.length + ")";
      } else {
        //検索ワードあり
        this.filterDataList = [];
        for (let i = 0; i < this.allRowDataList.length; i++) {
          const gameData = this.allRowDataList[i];
          if (gameData["name"].toUpperCase().indexOf(this.searchWord.toUpperCase()) > -1) {
            //一致
            this.filterDataList.push(gameData);
          }
        }
        this.searchLabel = "Search '" + this.searchWord + "' (" + this.filterDataList.length + ")";
      }
      console.log(this.searchLabel)
      this.pageIndex = 1;
      this.dispList();
    },
    dispList : function () {
      let max = 40 * this.pageIndex;
      if (max > this.filterDataList.length) {
        max = this.filterDataList.length;
        this.isNext = false;
      } else {
        this.isNext = true;
      }

      this.dispDataList = [];
      for (let i = 0; i < max; i++) {
        this.dispDataList.push(this.filterDataList[i]);          
      }
    },
    pushNext : function () {
      this.pageIndex = this.pageIndex + 1;
      this.dispList();
    },
    moveTop : function () {
      window.scrollTo(0, 0);   // スクロール位置を移動
    },
    pushMode : function (val) {
      this.$root.mode = val;
      localStorage.mode = val;
    },
    pushLang : function (index) {
      this.langIndex = index;
      console.log(index);
    },
    getGameList : function () {
      document.getElementById('collapse1').classList.remove('show');
      document.getElementById('collapse2').classList.remove('show');

      axios.get(this.$root.SERVER_PATH + 'getGames')
      .then((response) => {
        let resData = response.data;
        console.log(resData);

        this.allRowDataList = [];
        this.allRowDataList = resData["retList"];
        this.filterDataList = this.allRowDataList;
        this.searchLabel = "Top Games (" + this.filterDataList.length + ")";
        this.pageIndex = 1;

        let count = 0;
        for (let index = 0; index < this.allRowDataList.length; index++) {
          count += this.allRowDataList[index]["order"];
        }
        console.log(count + "ハイシ");

        this.dispList();

      }).catch((error) => {
        console.log(error.response);
      });
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.$root.pageName = "Top";
      vm.getGameList();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.$root.pageName = "Top";
      vm.getGameList();
    });
  },
}
</script>
