<template>
<br>
<div class="error">
  <h3>{{ errorMsg }}</h3>
  <router-link v-show="index != 0" to="/">
    <span v-on:click.prevent.stop="pushHome" >Topに戻る</span>
  </router-link>

</div>
</template>

<script>

export default {
  name: 'Error',
  components: {
  },
  data() {
    return {
      errorMsg : "",
    };
  },
  computed : {
		
  },
  mounted () {
  },
  methods : {
    pushHome : function () {
      this.$router.push("/");
    },
    initAction : function () {
      let query = Object.assign({}, this.$route.query);
      // let curPage = query["page"];
      let errId = query["errorCode"]; 

      if (errId == this.$root.API_FAILED_PARAM) {
        //パラメータ異常
        this.errorMsg = "パラメータ異常を検知しました";
      } else if (errId == this.$root.API_FAILED_NODATA) {
        //データなし
        this.errorMsg = "該当のクイズが存在しないか、非公開です";
      }
    },
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      console.log("A");
      vm.initAction();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
}
</script>

