<template>
<br>
<div class="play" style="width:90%; margin:auto">
  <div>
    <div style="display:flex">
      <img :src="this.$root.ICON_PATH + 'quiz_' + this.quizData.quiz_id + '.jpg?' + this.$root.reloadValue" style="width:50px; height:50px" class="icon">
      <div style="width:100%; text-align:left; margin-left:10px;">
        <p class="font_m" style="background-color:#ffffff; padding:10px; border-radius:5px">{{ this.quizData.quiz_name}}{{ quizSubName }}</p>
      </div>
    </div>
    
    <div class="sp_line_xl" />

    <div id="quiz" class="moku">
      <div>
        <div v-show="qStatus == 0">
          <p class="font_xl"><b>クイズを開始します</b></p>
          <div class="sp_line_m" />

          <p class="font_l">ニックネームを設定してください</p>

          <div class="sp_line_m" />

          <div style="width:85%; margin:auto">
            <input type="text" v-model="nickName" style="width:100%" placeholder="ニックネームを入力してください">
            <p class="font_s" style="text-align:center">ニックネームは出題者が結果を見る際に表示されます</p>
          </div>
 
          <div class="sp_line_l" />

          <button type="button" v-on:click="pushBtn('PLAY')" class="btnBase" style="width:200px">プレイ</button>

          <div class="sp_line_m" />
          <div class="grayBar" />
          <div class="sp_line_m" />

          <button type="button" v-on:click="pushBtn('PLAY_SECRET')" class="btnBase" style="width:200px">匿名でプレイ</button>
          <div class="sp_line_m" />
        </div>
        <div v-show="qStatus == 1">
          <p class="font_xl">第{{ this.qIndex + 1 }}問</p>
          <div class="sp_line_m" />
          <div>
            <div style="border:3px solid #808080; padding:15px 10px 10px; border-radius:10px; text-align:left; position:relative; width:100%; height:150px; margin:auto">
              <span class="font_l" style="position:absolute; top:-15px; left:20px; background-color:#ffffff; padding:0 10px">問題</span>
              <span class="font_m">{{this.curQuestionData['quiz_body']}}</span>
            </div>

            <div class="sp_line_m" />

            <div style="position:relative; z-index:1">
              <button type="button" class="bo2 fc choice_base" :class="choiceClasses[0]" v-on:click="pushChoice(0)" style="width:100%;">{{ this.curQuestionData['choices1'] }}</button>
              <div class="sp_line_s" />
              <button type="button" class="bo2 fc choice_base" :class="choiceClasses[1]" v-on:click="pushChoice(1)" style="width:100%">{{ this.curQuestionData['choices2'] }}</button>
              <div class="sp_line_s" />
              <div v-show="this.curQuestionData['choices3'] != ''">
                <button type="button" class="bo2 fc choice_base" :class="choiceClasses[2]" v-on:click="pushChoice(2)" style="width:100%">{{ this.curQuestionData['choices3'] }}</button>
                <div class="sp_line_s" />
              </div>
              <div  v-show="this.curQuestionData['choices4'] != ''">
                <button type="button" class="bo2 fc choice_base" :class="choiceClasses[3]" v-on:click="pushChoice(3)" style="width:100%">{{ this.curQuestionData['choices4'] }}</button>
                <div class="sp_line_s" />
              </div>
            </div>
          </div>
        </div>
      </div>

      <div v-show="qStatus > 1">
        <p class="font_xl">結果発表</p>
        <div class="sp_line_m" />

        <div style="border:3px solid #808080; padding:15px 10px 10px; border-radius:10px; text-align:left; position:relative; width:100%; margin:auto">
          <span class="font_l" style="position:absolute; top:-15px; left:50%; transform: translate(-50%, 0%); background-color:#ffffff; padding:0 10px">得点</span>
          <div style="text-align:center">
            <span class="score_num" :class="score_num_100" />
            <span class="score_num" :class="score_num_10" />
            <span class="score_num" :class="score_num_1" />
            <span class="score_num score_num_ten" />
          </div>
          <div class="sp_line_m" />
          <p v-show="qStatus > 2" class="font_s" style="text-align:center">{{ this.questionList.length }} 問中 {{ this.correctCount }} 問 正解！</p>
        </div>

        <div class="sp_line_m" />        
        <div v-show="qStatus > 2">
          <button type="button" v-on:click="pushBtn('SHARE')" class="btnBase" style="width:100%">結果をシェアする</button>
          <div class="sp_line_s" />
          <button v-show="qStatus == 3" v-on:click="pushBtn('SHOW_RESULT')" type="button" class="btnBase" style="width:100%">答案を見る</button>
          <button v-show="qStatus == 4" v-on:click="pushBtn('CLOSE_RESULT')" type="button" class="btnBase" style="width:100%">答案を閉じる</button>
          <div class="sp_line_s" />
          <button type="button" v-on:click="pushBtn('RETRY')" class="btnBase" style="width:100%">再挑戦する</button>
          <div class="sp_line_s" />
          <button type="button" v-on:click="pushBtn('TOP')" class="btnBase" style="width:100%">TOPに戻る</button>

          <div class="sp_line_l" />
        </div>
      </div>
    </div>

    <div v-show="qStatus == 4" style="border:3px solid #808080; padding:15px 10px 10px; border-radius:10px; text-align:left; position:relative; width:100%; margin-top:30px">
      <div class="sp_line_l" />
      <span class="font_l" style="position:absolute; top:-15px; left:20px; background-color:#FFDDCE; padding:0 10px">答案一覧</span>
        <div v-for="(answerData, index) in this.answerList" :key="index">
          <p class="font_m">Q{{index + 1}} {{ this.questionList[index]["quiz_body"] }}</p>
          <div class="row">
            <div class="col-3">
              <img v-show="answerData['isCorrect'] == 0" :src="this.$root.IMG_PATH + 'play_chara_2.png'" style="width:100px">
              <img v-show="answerData['isCorrect'] == 1" :src="this.$root.IMG_PATH + 'play_chara_1.png'" style="width:100px">
            </div>
            <div class="col-9">
              <div class="sp_line_ss" />
              <p class="font_m" style="color:#7ed6df; text-align:right">選択 {{ answerData["select"] }}</p>
              <div class="sp_line_ss" />
              <p class="font_m" style="color:#ff7979; text-align:right">答え  {{ answerData["correct"] }}</p>
            </div>
          </div>
          <div class="grayBar" />
        </div>
    </div>

    <div class="sp_line_l" />

    <div style="border:3px solid #808080; padding:15px 10px 10px; border-radius:10px; text-align:left; position:relative; width:100%; margin:auto">
      <span class="font_l" style="position:absolute; top:-15px; left:20px; background-color:#FFDDCE; padding:0 10px">ひとこと</span>
      <span class="font_m">{{ this.quizData.description }}</span>
    </div>

    <div class="sp_line_l" />

    <div style="border:3px solid #808080; padding:15px 10px 10px; border-radius:10px; text-align:left; position:relative; width:100%; margin:auto">
      <span class="font_l" style="position:absolute; top:-15px; left:20px; background-color:#FFDDCE; padding:0 10px">リンク</span>
      <div v-for="(url, index) in this.linkList" :key="index">
        <div class="row" v-on:click="pushLink(index)" style="cursor:pointer">
          <div class="col-10" style="display:flex">
            <img :src="this.$root.IMG_PATH + this.$root.snsIcon(url) + '.png'" style="width:30px; height:30px;">
            <span class="font_s" style="margin:5px 0 0 10px">{{url}}</span>
          </div>
          <div class="col-2" style="text-align:right">
            <img :src="this.$root.IMG_PATH + 'btn_link_arrow.png'" style="height:30px">
          </div>
        </div>
        <div class="grayBar" v-show="this.linkList.length != 1 && index != this.linkList.length - 1" />
      </div>
    </div>

    <div class="sp_line_xl" />

  </div>
</div>
<div id="cardBody" :style="'position:absolute; width:800px; height:418px; left:1700px; top:20px; border: 1px solid; background-image: url(' + this.$root.IMG_PATH + 'card/bg_card.png); background-repeat:repeat'">
  <img class="icon" :src="this.$root.ICON_PATH + 'quiz_' + this.quizData.quiz_id + '.jpg'" style="position:absolute; left:30px; top:130px; width:250px;">
  <p :style="'position:absolute; left:0px; margin:auto; transform:translate(0%, -50%); top:60px; font-size:' + cardTitleFont + 'px; width:100%'">
    <b>{{ this.quizData.quiz_name }}</b>
  </p>
  <p style="position:absolute; right:10px; top:110px; font-size:40px"><b>{{cardQuizSubName}}</b></p>
  <div>
    <p style="position:absolute; right:100px; bottom:60px; font-size:90px"><b>{{ scoreValue }}点</b></p>
    <p style="position:absolute; right:100px; bottom:20px; font-size:30px"><b>{{ questionList.length + " 問中 " + correctCount + " 正解" }}</b></p>
  </div>
</div>
<!-- <div id="footer" style="text-align:right">
  <span style="font-size:20px; margin-right:20px; margin-bottom:20px">クイズ作成サイト ワタクシゴトより</span>
</div> -->
<popup-window ref="popup" @pushPlay="pushPlay" ></popup-window>
<div style="position:absolute; left:10000px">
  <div class="backgroundCorrect" style="position:absolute; left: 100px; width: 100px; height:100px" />
  <div class="backgroundIncorrect" style="position:absolute; left: 200px; width: 100px; height:100px" />
</div>
</template>

<script>
import axios from 'axios'
// import VueScrollTo from 'vue-scrollto'

export default {
  name: 'Play',
  components: {
    
  },
  data() {
    return {
      playerId : "",
      nickName : "",

      quizData : [],
      questionList : [],
      curQuestionData : null,

      qIndex : 0,
      isChoice : false,

      choiceClasses : ["choice_normal", "choice_normal", "choice_normal", "choice_normal"],
      
      linkList : [],

      scoreValue: 0,
      scoreDispValue: 0,
      answerList : [],
      correctCount : 0,

      qStatus : 0,

      cardScore : "",
      cardResult : "",

    };
  },
  mounted () {
    this.$nextTick(() => {
      //ニックネーム選択を出す
      console.log(this.$refs);
    });
  },
  computed : {
    score_num_100 : function () {
      if (Math.floor(this.scoreDispValue / 100) == 0) {
        return "score_num_none";
      } else {
        return "score_num_1";
      }
    },
    score_num_10 : function () {
      let val100 = Math.floor(this.scoreDispValue / 100);
      let val10 = Math.floor((this.scoreDispValue - val100 * 100) / 10);
      if (val10 == 0 && val100 != 1) {
        return "score_num_none";
      } else {
        return "score_num_" + val10;
      }
    },
    score_num_1 : function () {
      let val = Math.floor(this.scoreDispValue % 10);
      return "score_num_" + val;
    },
    quizSubName : function() {
      let retVal = "のワタクシゴト";
      if (this.quizData.quiz_sub_name != "") {
        retVal = retVal + "（" + this.quizData.quiz_sub_name + "）";
      }
      return retVal;
    },
    cardTitleFont : function () {
      if (this.quizData["quiz_name"] != null) {
        let val = 700 / this.quizData["quiz_name"].length;
        console.log("font " + val + " " + this.quizData["quiz_name"]);
        if (val > 80) {
          val = 80;
        }
        return val;
      }
      return 10;
    }
  },
  methods : {
    test : function () {
      this.isRadio = !this.isRadio;
    },
    initAction : function () {
      let quizId = this.$route.params.quizId;
      if (quizId == "" || quizId.length != 20) {
        //引数エラー
        this.$router.push("/");
      }

      //ニックネームとplayerIDを取得
      if (localStorage.getItem("NICK_NAME") == null) {
        localStorage.setItem("NICK_NAME", "");
        this.nickName = "";
      } else {
        this.nickName = localStorage.getItem("NICK_NAME");
      }

      if (localStorage.getItem("PLAYER_ID") == null) {
        //プレイヤーIDを生成
        let id = "";
        let str = "abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789";
        for (let i = 0; i < 30; i++) {
          let c = str.substr(Math.floor(Math.random() * str.length), 1);
          id = id + c;
        }
        console.log("id " + id);

        localStorage.setItem("PLAYER_ID", id);
        this.playerId = id;
      } else {
        this.playerId = localStorage.getItem("PLAYER_ID");
      }
      console.log(this.nickName + "   " + this.playerId);

      let params = new URLSearchParams();
      params.append('quiz_id', quizId);

      axios.post(this.$root.SERVER_PATH + "getQuizData", params)
      .then((response) => {
        let resData = response.data;
        if (!this.$root.checkServerResponse(response)) {
          return;
        }

        this.quizData = resData["quizData"];
        this.questionList = resData["questionList"];


        this.linkList = [];
        this.linkList.push("https://twitter.com/" + this.quizData["twitterId"]);

        if (this.quizData["url1"] != "") {
          this.linkList.push(this.quizData["url1"]);
        }
        if (this.quizData["url2"] != "") {
          this.linkList.push(this.quizData["url2"]);
        }
        if (this.quizData["url3"] != "") {
          this.linkList.push(this.quizData["url3"]);
        }

        // this.quizData["questions"] = 5;
        // let qCount = this.quizData["questions"];

        //1問目を設定
        this.curQuestionData = this.questionList[0];

      });
    },
    pushChoice : function (index) {
      console.log(this.$refs);
      if (this.isChoice == true) {
        return;
      }
      this.isChoice = true;
      console.log(this.curQuestionData["correct_no"]);
      this.choiceClasses[index] = "choice_select";
      this.choiceClasses[this.curQuestionData["correct_no"]] = "choice_correct";

      //選択した答えを追加
      let selValue = "";
      if (index == 0) {
        selValue = this.curQuestionData["choices1"];
      } else if (index == 1) {
        selValue = this.curQuestionData["choices2"];
      } else if (index == 2) {
        selValue = this.curQuestionData["choices3"];
      } else if (index == 3) {
        selValue = this.curQuestionData["choices4"];
      }
      let correctValue = "";
      if (this.curQuestionData["correct_no"] == 0) {
        correctValue = this.curQuestionData["choices1"];
      } else if (this.curQuestionData["correct_no"] == 1) {
        correctValue = this.curQuestionData["choices2"];
      } else if (this.curQuestionData["correct_no"] == 2) {
        correctValue = this.curQuestionData["choices3"];
      } else if (this.curQuestionData["correct_no"] == 3) {
        correctValue = this.curQuestionData["choices4"];
      }

      let isCorrect = 0;
      if (this.curQuestionData["correct_no"] == index) {
        console.log("正解");
        isCorrect = 1;
        this.correctCount += 1;
        this.$root.setBg("correct");
      } else {
        console.log("不正解");
        this.$root.setBg("incorrect");
      }

      this.answerList.push({
        isCorrect : isCorrect,
        select : selValue,
        correct : correctValue,
      });

      setTimeout(() => {
        //最終問題か
        if (this.qIndex == this.questionList.length - 1) {
          //最終問題
          //スコアを計算
          if (this.correctCount == 0) {
            this.scoreValue = 0;
          } else {
            this.scoreValue = Math.ceil(this.correctCount / this.questionList.length * 100.0);
          }
          console.log("score " + this.scoreValue);
          this.scoreDispValue = 0;

          //カードを画像化
          window.html2canvas(document.getElementById('cardBody'), {
            scale:1
          }).then(canvas => {
            this.$root.setBg("normal");

            console.log(canvas.width + " " + canvas.height);
            var base64 = canvas.toDataURL('image/jpeg');
            base64 = base64.replace(/^.*,/, '');

            //挑戦数更新
            let params = new FormData();
            params.append('quizId', this.quizData.quiz_id);
            params.append('playerId', this.playerId);
            params.append('nickName', this.nickName);
            
            params.append('card', base64);
            
            params.append('score', this.correctCount);
            params.append('quizCount', this.questionList.length);

            axios.post(this.$root.SERVER_PATH + "updateChallenge", params)
            .then((response) => {
              console.log(response.data);
              this.qStatus = 2;

              //結果アニメーション
              this.resultAnimation();
            })
            .catch((e) => {
              this.qStatus = 2;
              alert(e); 

              //結果アニメーション
              this.resultAnimation();
            }); 
          });
        } else {
          //次の問題
          this.qIndex++;
          this.choiceClasses = ["choice_normal", "choice_normal", "choice_normal", "choice_normal"];
          this.curQuestionData = this.questionList[this.qIndex];
          this.isChoice = false;
          this.$root.setBg("normal");
        }        
      }, 2000);
    },
    resultAnimation : function () {
      if (this.scoreValue == 0) {
        //0点の場合は即出しでいっか
        this.qStatus = 3;
        return;
      }

      const scoreAnime = function () {
        this.scoreDispValue++;
        console.log(this.scoreDispValue);
        
        let interval = 100;
        if (this.scoreDispValue == this.scoreValue) {
          // clearInterval(scoreAnime);
          this.qStatus = 3;
          return;
        } else if (this.scoreDispValue > 20) {
          interval = 40;
        } else if (this.scoreDispValue > 40) {
          interval = 30;
        } else if (this.scoreDispValue > 60) {
          interval = 20;
        } else if (this.scoreDispValue > 80) {
          interval = 10;
        }
        setTimeout(scoreAnime, interval);
      }.bind(this);

      setTimeout(scoreAnime, 100);
    },
    pushBtn : function (name) {
      if (name == "PLAY_SECRET") {
        //匿名でプレイ
        this.nickName = "匿名";
        this.qStatus = 1;
      } else if (name == "PLAY") {
        //ニックネームでプレイ
        localStorage.setItem("NICK_NAME", name);
        this.nickName = name;
        this.qStatus = 1;
      } else if (name == "SHOW_RESULT") {
        this.qStatus = 4;
      } else if (name == "CLOSE_RESULT") {
        this.qStatus = 3;
      }
    },
    pushAnswer : function (val) {
      console.log(val);
      // //DL用の画像を準備
      // window.html2canvas(document.getElementById('downloadDiv')).then(canvas => {
      //   //ここにcanvas変換後の処理を記述する
      //   let url = canvas.toDataURL("png");
      //   let link = document.getElementById("downloadLink");
      //   link.href= url;
      //   link.download = "ワタクシゴト_" + this.quizData["quiz_id"] + ".png";
      // });

      //得点を算出
      let correctCount = 0;
      for (let index = 0; index < this.correctList.length; index++) {
        if (this.correctList[index] == "1") {
          correctCount++;
        }
      }
      let score = Math.floor(correctCount / this.questionList.length * 100.0);
      this.resScore1 = score;
      this.resScore2 = this.questionList.length + "問中" + correctCount + "問正解";

      //カードを画像化
      window.html2canvas(document.getElementById('cardBody'), {
        scale:1
      }).then(canvas => {
        console.log(canvas.width + " " + canvas.height);
        var base64 = canvas.toDataURL('image/jpeg');
        base64 = base64.replace(/^.*,/, '');

        //挑戦数更新
        let params = new FormData();
        params.append('quiz_id', this.quizData.quiz_id);
        params.append('score', this.resScore1);
        params.append('card', base64);

        axios.post(this.$root.SERVER_PATH + "updateChallenge", params);      
      });

      this.isSelect = false;
      this.checkIndex = 0;

      const checkAnswer = function() {
        let targetId = "";
        if (this.checkIndex != this.quizData.questions) {
          targetId = 'q' + this.checkIndex;
        } else {
          targetId = 'result';
        }
        var element = document.getElementById(targetId);
        var rect = element.getBoundingClientRect();
        var elemtop = rect.top + window.pageYOffset;
        console.log(targetId + " " + elemtop);
        console.log(window.pageYOffset);
        document.documentElement.scrollTop = elemtop;

        console.log("a " + window.scrollY);
        const checkAnswer2 = function() {
          console.log("b " + window.scrollY);
          if (this.checkIndex != this.quizData.questions) {
            if (this.answerList[this.checkIndex] == this.questionList[this.checkIndex]["correct_no"]) {
              this.correctList.push("1");
            } else {
              this.correctList.push("0");
            }

            console.log("B" + this.checkIndex)
            this.checkIndex++;
            setTimeout(checkAnswer, 1000);
          } else {
            console.log("おわり");

            this.checkKbn = 1;
            const checkAnswer3 = function () {
              this.checkKbn = 2;
            }.bind(this);
            setTimeout(checkAnswer3, 1500);
          }
        }.bind(this);
        // console.log(checkAnswer2);
        setTimeout(checkAnswer2, 1000);
      }.bind(this);
      checkAnswer();
    },
    pushResult : function (kbn) {
      if (kbn == "SHARE") {
        console.log("SHARE");
      } else if (kbn == "DOWNLOAD") {
        console.log("DOWNLOAD");
      } else if (kbn == "RETRY") {
        this.answerList = [];
        this.correctList = [];
        for (let index = 0; index < this.quizData["questions"]; index++) {
          this.answerList.push("0");
        }
        this.checkKbn = 0;
        this.isSelect = true;
        window.scrollTo(0, 0);
      }
    },
    pushLink : function () {
      let modalLinkList = [];
      modalLinkList.push("https://twitter.com/" + this.quizData["twitterId"]);

      if (this.quizData["url1"] != "") {
        modalLinkList.push(this.quizData["url1"]);
      }
      if (this.quizData["url2"] != "") {
        modalLinkList.push(this.quizData["url2"]);
      }
      if (this.quizData["url3"] != "") {
        modalLinkList.push(this.quizData["url3"]);
      }
      this.$refs.popup.modalLinkList = modalLinkList;
      this.$refs.popup.showModal("LINK");
    },
    hinan : function() {
//         <!-- <div class="container-fluid" v-show="this.isSelect">
//     <div class="row">
//       <div class="col-6">
//         <router-link to="/" class="btnBase" >
//           <span v-on:click.prevent.stop="pushAnswer(0)">解答あり採点</span>
//         </router-link>
//       </div>
//       <div class="col-6">
//         <router-link to="/" class="btnBase" >
//           <span v-on:click.prevent.stop="pushAnswer(1)">解答なし採点</span>
//         </router-link>
//       </div>
//     </div>
//   </div>
//   <br>
//   <div id="result">
//     <br>
//     <div v-show="this.checkKbn != 0" style="width:80%; background-color:#ffbe76; padding:20px; text-align:center; margin:auto">
